*{
  padding: 0;
  margin: 0;
}

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*, *:before, *:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

.calendar-container {
  border: 2px solid black;
}

.calendar {
  background-color: white;
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

.calendar-header {
  border-bottom: 1px solid black;
  font-size: 1.6em;
}

.calendar-header td {
  border-spacing: 0;
  padding-left: 5px;
}

.day,
.emptySlot {
  border: 1px solid rgba(0,0,0,.2);
  width: 14%;
  height: 100px!important;
}

.weekdays {
  font-size: 0.8em;
  text-align: center;
  height: 40px;
}

.day {
  font-size: 0.8em;
  line-height: 24px;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.day span:first-of-type {
  position: absolute;
  top: 0;
  right: 0;
  text-align: right;
  padding-right: 5px;
  width: 100%;
}

.day span:last-of-type {
  position: absolute;
  bottom: 0;
  left: 0;
  text-align: left;
  padding-left: 5px;
  color: rgba(0,0,0,.2);
  width: 50%;
}

.current-day {
  background-color: #4284;
  color: white;
}



